@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.app__header {
  border-bottom: 1px solid rgba(220, 220, 220, 1);
  box-shadow: 2px 2px 2px rgba(220, 220, 220, 1);
  padding: 20px;
}

.header-container {
  width: 980px;
  margin: 0 auto;
}

.nav-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 20px;
}

.navbar-lists {
  display: flex;
  flex-direction: row;
}

.navbar-lists .navbar-item {
  margin-right: 10px;
}

/* On screens that are 992px wide or less, the background color is blue */
@media screen and (max-width: 992px) {
  .header-container {
    width: 700px;
  }
}

/* On screens that are 600px wide or less, the background color is olive */
@media screen and (max-width: 600px) {
  .header-container {
    width: 100%;
  }
}

.story-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.story-card {
  width: 100px;
  max-width: 250px;
  box-sizing: border-box;
  box-shadow: 0px 0px 6px 2px rgba(200, 200, 200, 1);
  margin-right: 10px;
  border-radius: 50%;
  display: block;
  margin-bottom: 8px;
  cursor: pointer;
  max-height: 250px;
  min-height: 100px;
}
.story-card .story-image {
  border-radius: 50%;
  max-width: 100%;
  object-fit: contain;
  border: 2px solid #111111;
  display: block;
  height: 100%;
  width: auto;
  width: initial;
}

.story__username {
  text-align: center;
  text-overflow: ellipsis;
  width: 100px;
  overflow: hidden;
  white-space: nowrap;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #262626;
  transition: 0.3s all;
  z-index: 99;
}

.modal-wrapper {
  position: fixed;
  background: rgba(255, 255, 255, 0.8);
  width: 50%;
  height: 75%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal_action {
  position: relative;
  height: 100%;
  flex-grow: 1;
}

.modal_action .navigation-wrapper {
  background: #4caf50;
  width: 25px;
  position: absolute;
  top: 50%;
  text-align: center;
  padding: 0.5rem;
  border-radius: 50%;
  height: 25px;
  font-weight: 700;
  display: block;
  line-height: 1;
  cursor: pointer;
  transition: 0.3s;
  color: #f1f1f1;
}

.modal_action .navigation-wrapper:hover {
  background: #f1f1f1;
  color: #4caf50;
}

.navigation-wrapper .action-icon {
  font-size: 24px;
}

.navigation-wrapper.navigation--previous {
  left: 20%;
}

.navigation-wrapper.navigation--next {
  right: 20%;
}

.navigation-close {
  color: black;
  font-size: 24px;
  background: #ddd;
  width: 40px;
  height: 40px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 23%;
  top: 9%;
  z-index: 1;
  border-radius: 50%;
  box-shadow: 0px 0 0px 2px rgba(240, 240, 240, 1);
  cursor: pointer;
}

.stories_image {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.stories-container {
  width: 980px;
  margin: 0 auto;
  position: -webkit-sticky;
  position: sticky;
  top: -61px;
  z-index: 9;
}
.stories-cards {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 1);
  border: 1px solid rgba(240, 240, 240, 1);
  border-radius: 12px;
}

.stories-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  font-size: 20px;
  font-weight: 500;
  min-width: 59%;
}
.stories-heading .stories-all {
  cursor: pointer;
  text-decoration: underline;
  transition: 0.3s;
}
.stories-heading .stories-all:hover {
  cursor: pointer;
  color: blueviolet;
}

.stories-lists {
  display: flex;
  margin-top: 16px;
  overflow: scroll;
}

.stories-lists::-webkit-scrollbar {
  display: none;
}

/* On screens that are 992px wide or less, the background color is blue */
@media screen and (max-width: 992px) {
  .stories-container {
    width: 700px;
  }
}

/* On screens that are 600px wide or less, the background color is olive */
@media screen and (max-width: 600px) {
  .stories-container {
    width: 100%;
  }
}

.post-card {
  width: 100%;
  height: 100%;
  position: relative;
  box-shadow: 0px 0px 6px 2px rgba(200, 200, 200, 1);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 10px 8px;
  margin-bottom: 10px;
  border-radius: 8px;
}

.post__header,
.post__user {
  display: flex;
}

.post__header {
  border-bottom: 1px solid;
  padding-bottom: 10px;
}

.post__user .post__avatar {
  display: block;
  max-width: 50px;
  max-height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.post__user .post__username {
  display: block;
  font-size: 18px;
  padding-left: 10px;
  font-weight: 600;
}

.post__user .post__email {
  font-size: 16px;
  font-weight: normal;
}

.post__content .content__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.posts-container {
  width: 980px;
  margin: 0 auto;
}

/* On screens that are 992px wide or less, the background color is blue */
@media screen and (max-width: 992px) {
  .posts-container {
    width: 700px;
  }
}

/* On screens that are 600px wide or less, the background color is olive */
@media screen and (max-width: 600px) {
  .posts-container {
    width: 100%;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans", sans-serif;
  box-sizing: border-box;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* common css class */
.pt-1 {
  padding-top: 1rem;
}

img {
  display: block;
}

.mt-10 {
  margin-top: 10px;
}

.app-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background: #ffffff;
  color: #1b3759;
  min-height: 100vh;
}

